<script lang="ts">
  import type { Mod, Maybe, Version } from '$lib/generated';
  import { assets, base } from '$app/paths';
  import Card, { Content, Actions } from '@smui/card';
  import IconButton, { Icon } from '@smui/icon-button';
  import { goto } from '$app/navigation';

  export let mod: Pick<Mod, 'id' | 'name' | 'logo' | 'views' | 'downloads' | 'short_description'> & {
    latestVersions: {
      alpha?: Maybe<Pick<Version, 'id'>>;
      beta?: Maybe<Pick<Version, 'id'>>;
      release?: Maybe<Pick<Version, 'id'>>;
    };
  };

  /* eslint-disable @typescript-eslint/no-unused-vars */
  $: logo = mod.logo || assets + '/images/no_image.png';

  $: installable =
    'latestVersions' in mod ? mod.latestVersions.alpha || mod.latestVersions.beta || mod.latestVersions.release : false;
</script>

<Card style="height: 100%">
  <div class="grid sm:grid-cols-2 grid-cols-1 justify-items-center">
    <div style="max-width: 200px; max-height: 200px; cursor: pointer" on:click={() => goto(base + `/mod/${mod.id}/`)}>
      <img
        crossorigin="anonymous"
        src={logo}
        alt="{mod.name} Logo"
        style="max-width: 100%; max-height: 100%; min-width: 200px; min-height: 200px;"
        class="logo"
      />
    </div>
    <div style="display: flex; flex-direction: column; justify-content: space-between; width: 100%">
      <Content style="display: flex; flex-direction: column; padding-bottom: 0;">
        <div style="margin-bottom: 10px">
          <a href="{base}/mod/{mod.id}/">
            <span class="text-xl text-gray-50 break-all">{mod.name}</span>
          </a>

          <h5 class="text-sm" style="margin: 0;">
            <span>
              <Icon class="material-icons" style="font-size: inherit; vertical-align: middle;">visibility</Icon>
              {mod.views}
            </span>
            <span>
              <Icon class="material-icons" style="font-size: inherit; vertical-align: middle;">download</Icon>
              {mod.downloads}
            </span>
          </h5>
        </div>

        <div class="break-all">
          {mod.short_description}
        </div>
      </Content>

      <Actions style="align-self: end;">
        <IconButton href="{base}/mod/{mod.id}/" aria-label="Go to mod page" title="Go to mod page">
          <Icon class="material-icons">info</Icon>
        </IconButton>
        {#if installable}
          <!-- TODO SMM Button -->
          <IconButton class="material-icons" title="Install">download</IconButton>
        {/if}
      </Actions>
    </div>
  </div>
</Card>

<style lang="postcss">
  .logo {
    border-top-left-radius: 4px;
    border-bottom-left-radius: 4px;
  }
</style>
